import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import ReactTyped from 'react-typed'
import CIcon from '@coreui/icons-react'
import { cilWallet } from '@coreui/icons'

const Greetings = ({ user }) => {
  const [greeting, setGreeting] = useState('')

  function getGreeting() {
    const date = new Date()
    const hour = date.getHours()
    if (hour < 12) return setGreeting('Good Morning')
    if (hour < 18) return setGreeting('Good Afternoon')
    return setGreeting('Good Evening')
  }
  useEffect(() => {
    getGreeting()
  }, [])
  return (
    <div className="d-flex flex-column justify-content-between flex-md-row">
      <h5>
        Hello, {user && user.name} <ReactTyped strings={[greeting]} typeSpeed={200} loop />{' '}
      </h5>
      <div className="d-flex">
        <div
          className="bg-white mb-2 "
          style={{
            border: '1px solid rgb(65, 117, 252)',
            borderStyle: 'dotted',
            marginLeft: '6rem',
            borderRadius: '10px',
            padding: '.7rem',
            color: 'rgb(65, 117, 252)',
          }}
        >
          <CIcon className="" icon={cilWallet} /> Balance : ₹{user && user.walletBalance}
        </div>
        {user && user.role === 'retailer' ? null : (
          <div
           className="bg-white mb-2 ms-0 ms-md-2"
          style={{
            border: '1px solid rgb(65, 117, 252)',
            borderStyle: 'dotted',
            borderRadius: '10px',
            padding: '.7rem',
            color: 'rgb(65, 117, 252)',
          }}
          >
            Total Earned : ₹{user.totalCommissionEarned}
          </div>
        )}
      </div>
    </div>
  )
}

export default Greetings
Greetings.propTypes = {
  user: PropTypes.object,
}
